.footer {
  min-height: 20px;
  background: #4537c2;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  color: white;
  text-align: center;
}

.footer span {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 3px;
  background: #4537c2;
  color: white;
  text-align: center;
}
